chrome.runtime.onInstalled.addListener(function (details) {
	if (details.reason === 'install') {
		chrome.tabs.create({ url: 'https://copyleaks.com/thank-you-for-installing-ai-detector-extension' });
	}
});
chrome.runtime.setUninstallURL(
	'https://docs.google.com/forms/d/e/1FAIpQLScY0yNozrzURN8BJi6xfFysh-_eHteIDuZNsi6piKyQ5FZ7QQ/viewform'
);
